<template>
  <div class="card-item">
    <div class="header">
      <slot name="header">标题</slot>
    </div>
    <div class="img" v-if="!textMode">
      <img :src="computedImgSrc" alt="">
    </div>
    <div class="content" v-if="textMode">
      <slot name="content"></slot>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    imgSrc: String,
    textMode: Boolean
  },
  computed: {
    computedImgSrc () {
      if (this.imgSrc === '') return ''
      return require(`@/assets/imgs/${this.imgSrc}`)
    }
  }
}
</script>

<style scoped lang="less">
.card-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);

  .header {
    font-size: 20px;
    font-weight: bold;
  }

  .img {
    margin-top: 20px;
    text-align: center;
    width: 80%;
    background-color: rgba(0, 0, 0, 0.1);
    img {
      min-height: 100px;
      max-height: 300px;
    }
  }

  .content {
    width: 100%;
    margin-top: 20px;
  }
}
</style>
