<template>
  <div class="home-page">
    <Card :textMode="true" v-for="(text, index) in texts" :key="index">
      <template #header> {{ textFileNames[index] }} </template>
      <template #content>
        {{ text.default }}
      </template>
    </Card>
  </div>
</template>

<script>
import Card from '@/components/mobile/CardItem.vue'
export default {
  components: {
    Card
  },
  data () {
    return {
      texts: [],
      textFileNames: []
    }
  },
  created () {
    this.loadTxtFiles()
  },
  methods: {
    loadTxtFiles () {
      const requireTxt = require.context('@/assets/articles', false, /\.txt$/) // 获取该文件夹中的所有txt文件
      this.textFileNames = requireTxt.keys()
      this.textFileNames = this.textFileNames.map(item => item.replace('.txt', ''))
      this.textFileNames = this.textFileNames.map(item => item.replace('./', ''))
      const texts = []
      requireTxt.keys().forEach((fileName) => {
        const fileContent = requireTxt(fileName) // 读取文件内容
        fileContent.default.trim()
        texts.push(fileContent) // 将内容存入数组
      })
      this.texts = texts // 将所有文本内容赋值给 data 中的 texts
    }
  }
}
</script>

<style lang="less" scoped>
.home-page {
  width: 100%;
}
</style>
