<template>
  <div class="home-page">
    <Card v-for="(imgSrc, index) in images" :key="index" :imgSrc="imgSrc" :textMode="false">
      <template #header>{{ imgSrc.replace(/\.[^/.]+$/, '') }}</template>
    </Card>
  </div>
</template>

<script>
import Card from '@/components/Device/CardItem.vue'
export default {
  components: {
    Card
  },
  data () {
    return {
      images: []
    }
  },
  created () {
    // 使用 require.context 读取文件夹中的所有文件
    const imagesContext = require.context('@/assets/imgs', false, /\.(png|jpe?g|gif)$/)

    // 获取文件路径列表并赋值给 images
    this.images = imagesContext.keys().map(imagePath => {
      // 使用正则提取文件名
      return imagePath.replace(/^.*[\\/]/, '')
    })
  }
}
</script>

<style lang="less" scoped>
.home-page {
  margin: 10px auto;
  width: 694px;
  background-color: #fff;
  box-shadow:
  2px 2px 4px rgba(0, 0, 0, 0.1),
  -2px 2px 4px rgba(0, 0, 0, 0.1),
  2px -2px 4px rgba(0, 0, 0, 0.1),
  -2px -2px 4px rgba(0, 0, 0, 0.1);  /* 微弱的阴影 */
}
</style>
