<template>
  <div class="home-page">
    <CardItem v-for="(imgSrc, index) in images" :key="index" :imgSrc="imgSrc" :textMode="false">
      <template #header>
        {{ imgSrc.replace(/\.[^/.]+$/, '') }}
      </template>
    </CardItem>
  </div>
</template>

<script>
import CardItem from '@/components/mobile/CardItem.vue'
export default {
  components: {
    CardItem
  },
  created () {
    this.loadImgFiles()
  },
  methods: {
    loadImgFiles () {
      // 使用 require.context 读取文件夹中的所有文件
      const imagesContext = require.context('@/assets/imgs', false, /\.(png|jpe?g|gif)$/)

      // 获取文件路径列表并赋值给 images
      this.images = imagesContext.keys().map(imagePath => {
      // 使用正则提取文件名
        return imagePath.replace(/^.*[\\/]/, '')
      })
    }
  }
}
</script>

<style lang="less" scoped>
.home-page {
  width: 100%;
}
</style>
