import Vue from 'vue'
import VueRouter from 'vue-router'

import LayoutMobile from '@/views/mobile/layout/index.vue'
import LayoutDevice from '@/views/device/layout/index.vue'

import HomePageMobile from '@/views/mobile/layout/HomePageItem.vue'
import HomePageDevice from '@/views/device/layout/HomePageItem.vue'

import AboutPageMobile from '@/views/mobile/layout/AboutPageItem.vue'
import AboutPageDevice from '@/views/device/layout/AboutPageItem.vue'

import MiniGamePageDevice from '@/views/device/layout/MiniGamePageItem.vue'
import MiniGamePageMobile from '@/views/mobile/layout/MiniGamePageItem.vue'

import ArticlesPageDevice from '@/views/device/layout/articles-page.vue'
import ArticlesPageMobile from '@/views/mobile/layout/articles-page.vue'

import ForgiveMeItem from '@/views/device/minigame/ForgiveMeItem.vue'

Vue.use(VueRouter)

function isMobile () {
  return window.innerWidth <= 768
}

const routes = [
  {
    path: '/',
    component: isMobile() ? LayoutMobile : LayoutDevice,
    children: [
      {
        path: '',
        redirect: 'home'
      },
      {
        path: 'home',
        name: 'home',
        component: isMobile() ? HomePageMobile : HomePageDevice
      },
      {
        path: 'about',
        name: 'about',
        component: isMobile() ? AboutPageMobile : AboutPageDevice
      },
      {
        path: 'minigame',
        name: 'minigame',
        component: isMobile() ? MiniGamePageMobile : MiniGamePageDevice
      },
      {
        path: 'articles',
        name: 'articles',
        component: isMobile() ? ArticlesPageMobile : ArticlesPageDevice
      }
    ]
  },
  {
    path: '/minigame/forgiveme',
    name: 'forgiveme',
    component: ForgiveMeItem
  },
  {
    path: '*',
    redirect: '/'
  }
]

const router = new VueRouter({
  routes
})

export default router
