<!-- 该页面参考about-page中的内容 -->
<template>
  <div class="home-page">
    <Card :textMode="true" v-for="(text, index) in texts" :key="index">
      <template #header>文章{{ index+1 }}</template>
      <template #content>
          {{ text.default }}
      </template>
    </Card>
  </div>
</template>

<script>
import Card from '@/components/Device/CardItem.vue'
export default {
  components: {
    Card
  },
  data () {
    return {
      texts: []
    }
  },
  created () {
    this.loadTxtFiles()
  },
  methods: {
    loadTxtFiles () {
      const requireTxt = require.context('@/assets/articles', false, /\.txt$/) // 获取该文件夹中的所有txt文件
      const texts = []
      requireTxt.keys().forEach((fileName) => {
        const fileContent = requireTxt(fileName) // 读取文件内容
        fileContent.default.trim()
        texts.push(fileContent) // 将内容存入数组
      })
      this.texts = texts // 将所有文本内容赋值给 data 中的 texts
    }
  }
}
</script>

<style lang="less" scoped>
.home-page {
  margin: 10px auto;
  width: 694px;
  min-height: 92vh;
  background-color: #fff;
  box-shadow:
  2px 2px 4px rgba(0, 0, 0, 0.1),
  -2px 2px 4px rgba(0, 0, 0, 0.1),
  2px -2px 4px rgba(0, 0, 0, 0.1),
  -2px -2px 4px rgba(0, 0, 0, 0.1);  /* 微弱的阴影 */
}
</style>
