<template>
  <div class="Card">
    <div class="header">
      <slot name="header">
        这是标题
      </slot>
    </div>
    <img v-if="computedImgSrc" :src="computedImgSrc" alt="">
    <div class="content" v-if="textMode" :class="show ? 'allContent' : 'briefContent'">
      <slot name="content">这是内容</slot>
      <button @click="showContent">
        {{  this.show ? '收起' : '展开'}}
      </button>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      show: false
    }
  },
  props: {
    imgSrc: String,
    fromHome: Boolean,
    textMode: Boolean
  },
  computed: {
    computedImgSrc () {
      if (!this.imgSrc) return ''
      return require(`@/assets/imgs/${this.imgSrc}`)
    }
  },
  methods: {
    showContent () {
      this.show = !this.show
    },
    loadTxtFiles () {
      const requireTxt = require.context('@/assets/daily-notes', false, /\.txt$/) // 获取该文件夹中的所有txt文件
      const texts = []
      requireTxt.keys().forEach((fileName) => {
        const fileContent = requireTxt(fileName) // 读取文件内容
        fileContent.default.trim()
        texts.push(fileContent) // 将内容存入数组
      })
      this.texts = texts // 将所有文本内容赋值给 data 中的 texts
    }
  }
}
</script>

<style scoped lang="less">
.Card {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: calc(60 / 15 * 1vmin);
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);

  .header {
    margin-bottom: 2px;
    font-weight: 500;
    font-size: calc(7 * 1vmin);
  }

  img {
    min-height: calc(40 * 1vmin);
    max-height: calc(60 * 1vmin);
  }

  .content {
    position: relative;

    &.briefContent {
      overflow: hidden;
      max-height: calc(80 / 15 * 2vmin);
      button {
        color: #1772f6;
        font-size: calc(56 / 15 * 1vmin);
        &::before {
          background: linear-gradient(to left, rgba(255, 255, 255, 1) 0%, rgba(255, 255, 255, 1) 60%, rgba(255, 255, 255, 0)100%);;
        }
      }
    }
    &.allContent {
      button {
        color: #8e8e8e;
        font-size: calc(40 / 15 * 1vmin);
        &::before {
          background: transparent;
        }
      }
    }

    font-size: calc(56 / 15 * 1vmin);
    line-height: calc(80 / 15 * 1vmin);

    button {
      &::before {
        content: '';

        position: absolute;
        top: 0;
        right: 0;

        display: block;
        height: calc(80 / 15 * 1vmin);
        width: 5em;

        z-index: -1;
      }
      position: absolute;
      right: 0;
      bottom: 0;
      border: none;
      background: none;
      z-index: 1;
    }
  }
}
</style>
