<template>
<div class="header">
  <ul>
    <li><router-link to="/home">首页</router-link></li>
    <li><router-link to="/about">关于我们</router-link></li>
    <li><router-link to="/minigame">小游戏大全</router-link></li>
    <li><button @touchstart="handleTouchStart" @touchend="handleTouchEnd"></button></li>
  </ul>
</div>
</template>

<script>
export default {
  data () {
    return {
      preessTimer: null,
      pressDuration: 3000
    }
  },
  methods: {
    handleTouchStart () {
      this.preessTimer = setTimeout(() => {
        this.onLongPress()
      }, this.pressDuration)
    },
    handleTouchEnd () {
      clearTimeout(this.preessTimer)
      this.preessTimer = null
    },
    onLongPress () {
      this.$router.push('/articles')
    }
  }

}
</script>

<style lang="less" scoped>
.header {
  ul {
    display: flex;
    flex-direction: row;
    margin: 0;
    padding: 0;
    a {
      display: block;
      padding: 0 calc(64 / 15 * 1vmin);
      height: calc(32 / 3 * 1vmin);
      line-height: calc(32 / 3 * 1vmin);
      font-size: calc(56 / 15 * 1vmin);
      color: #505050;
      text-decoration: none;
      &.router-link-active {
        color: #1772f6;
        border-bottom: #1772f6 solid 2px;
      }
    }
    button {
      display: block;
      background-color: transparent;
      border: none;
      width: 50px;
      height: 100%;
      cursor: default;
      outline: none;
    }
  }
}
</style>
