<template>
<div class="header">
  <ul>
    <li><router-link to="/home">首页</router-link></li>
    <li><router-link to="/about">关于我们</router-link></li>
    <li><router-link to="/minigame">小游戏大全</router-link></li>
    <li><button @click="specialWork"></button></li>
  </ul>
</div>
</template>

<script>
export default {
  data () {
    return {
      clickTimestamps: []
    }
  },
  methods: {
    specialWork () {
      const currentTime = Date.now()
      this.clickTimestamps.push(currentTime)
      this.clickTimestamps = this.clickTimestamps.filter(timestamp => currentTime - timestamp <= 2000)
      if (this.clickTimestamps.length >= 5) {
        this.$router.push('/articles')
      }
      setTimeout(() => {
        if (this.clickTimestamps.length >= 5) {
          this.clickTimestamps = []
        }
      }, 2000)
    }
  }
}
</script>

<style lang="less" scoped>
.header {
  box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.2);
  ul {
    display: flex;
    flex-direction: row;
    margin: 0;
    padding: 0;
    height: 52px;
    margin-left: 25px;
    li {
      height: 50px;
      padding: 0 15px;
      a {
        display: block;
        padding: 0;
        height: 50px;
        line-height: 50px;
        font-size: 16px;
        color: #191B1F;;
        text-decoration: none;
        &.router-link-active {
          font-weight: 700;
          color: #1772f6;
          border-bottom: #1772f6 solid 5px;
        }
      }
      button {
        display: block;
        background-color: transparent;
        border: none;
        width: 50px;
        height: 100%;
        cursor: default;
        outline: none;
      }
    }
  }
}
</style>
